export default {
	data() {
		return {
			email: '',
			password: '',
			disable: true,
			loading: false
		}
	},
	watch: {
		email (val) {
			this.change();
		},
		password (val) {
			this.change();
		},
	},
	methods: {
		// 监听输入框
		change () {
			if (this.email && this.password) {
				this.disable = false;
				return;
			}
			this.disable = true;
		},
		// 验证
		check () {
			if (this.email && this.password && this.email != "" && this.password != "") {	
				
				// 验证邮箱格式
				let pattern = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
				
				if (!pattern.test(this.email)) {
					uni.showToast({
						title: '请输入正确的邮箱格式',
						icon: 'none'
					});
					return false;
				}
				return true;
			}
			
			uni.showToast({
				title: '请确认数据填写完整',
				icon: "none"
			})
			
			return false;
			
		},
		// 提交
		submit () {
			
			this.loading = true;
			this.disable = true;
			
			if (!this.check()) {
				this.loading = false;
				this.disable = false;
				return;
			}
			uni.showToast({
				title: '验证通过',
				mask: false,
				duration: 1500
			});
			this.loading = false;
			this.disable = false;
		}
	}
}
